@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

::-moz-selection {
  background: yellow;
}

::selection {
  background: yellow;
}

.App {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  font-size: calc(10px + 4vmin);

  background-color: #283430;
  /*background-color: #34282c;*/

  font-family: "Permanent Marker", cursive;
}

.BodyRow {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.3);
}

.BodyRow:nth-child(even) {
  color: black;
  background-color: white;
}

.BodyRow:nth-child(odd) {
  color: white;
}

.InitialDescription {
  width: 50%;
  font-style: italic;
}

.TextHighLight {
  color: yellow;
  font-size: 75px;
}

.ProfilesContainer {
  max-height: 100%;
  max-width: 20%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 10vmin;
}

.ProfileWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  font-size: 3vmin;
  white-space: nowrap;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-decoration: none;
}

.ProfileWrapper:not(:first-child) {
  margin-top: 3vmin;
}

.ProfileWrapper:hover .ProfilePicture {
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
}

.ProfileWrapper span {
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  text-decoration: none;
  color: white;
}

.ProfileWrapper:hover span {
  color: yellow;
}

.ProfilePicture {
  display: flex;
  border-radius: 50%;
  margin-right: 3vmin;
  flex-basis: 1px;
  flex-grow: 0;
  object-fit: contain;
  max-width: 30%;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.3);
  -webkit-transition: box-shadow 0.3s, -webkit-filter 0.3s, -webkit-transform 0.3s;
  transition: box-shadow 0.3s, -webkit-filter 0.3s, -webkit-transform 0.3s;
  transition: filter 0.3s, transform 0.3s, box-shadow 0.3s;
  transition: filter 0.3s, transform 0.3s, box-shadow 0.3s, -webkit-filter 0.3s, -webkit-transform 0.3s;
  pointer-events: none;
}

.SectionTitle,
.SectionTitleInv {
  position: absolute;
  top: 5vh;
  padding-left: 2vmin;
  padding-right: 2vmin;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.3);

  border-radius: 10px;
}

.SectionTitle {
  color: white;
  background-color: #283430;
}

.SectionTitleInv {
  color: #283430;
  background-color: white;
}

.ScrollContainer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 4vmin;
  width: 75px;
  cursor: pointer;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.ScrollContainer:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.White {
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

.ChevronDown,
.ChevronUp {
  position: absolute;
  top: 50%;
  font-size: 30px;
}

.ChevronDown {
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.ChevronUp {
  -webkit-transform: translateY(-55%) rotate(180deg);
          transform: translateY(-55%) rotate(180deg);
}

.ScrollButton {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.MeshImage {
  position: absolute;
  width: 10vmin;
  height: 10vmin;
}

.MeshImage:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.MeshImage:nth-child(1) {
  left: 10vmin;
  top: 10vmin;
}

.MeshImage:nth-child(2) {
  right: 10vmin;
  bottom: 10vmin;
}
.MeshImage:nth-child(3) {
  left: 10vmin;
  bottom: 10vmin;
}
.MeshImage:nth-child(4) {
  right: 10vmin;
  top: 10vmin;
}

.SlideShowContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 50%;
}

.slick-list {
  overflow: visible;
}

.SlickNextButton,
.SlickPreviousButton {
  position: absolute;
  width: 50px;
  top: 50%;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
  cursor: pointer;
  box-shadow: 10px black;
  opacity: 0.75;
}

.SlickNextButton:hover,
.SlickPreviousButton:hover {
  opacity: 1;
}

.SlickPreviousButton {
  left: -25px;
  -webkit-transform: translate(-100%, -100%);
          transform: translate(-100%, -100%);
}

.SlickNextButton {
  right: -25px;
  -webkit-transform: translate(100%, -100%);
          transform: translate(100%, -100%);
}

.SlideShowImage {
  position: relative;
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 0 16px 2px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  max-height: 60vh;
  max-width: 100%;

  border-style: solid 2px #283430;
}

.ImageDescription {
  width: 100%;
  text-align: center;
  font-size: 30px;
}

